// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
// 路徑
import router from './router'
// httprequest
import axios from 'axios'
// 公用變數
import store from '../store/index.js'
// callWS
import CallAPI from './assets/js/gows'
// excel匯出
import JsonExcel from 'vue-json-excel'
// country
import country from './assets/js/country.js'
// func
import func from './assets/js/func'
// import swal from 'vue-sweetalert2';

import 'sweetalert2/dist/sweetalert2.min.css';
// import './assets/css/dashboard.css'
import './assets/css/style.css'
import './assets/css/bootstrap.min.css'
import './assets/js/popper.min.js'
import './assets/js/bootstrap.min.js'
import './assets/js/page-flip.browser.min.js'

Vue.config.productionTip = false
Vue.prototype.$axios = axios
Vue.prototype.$CallAPI = CallAPI
Vue.prototype.$country = country
// Vue.prototype.$setUser = setUser
const Swal = require('sweetalert2')
Vue.prototype.$swal = Swal
Vue.prototype.$func = func

Vue.component('downloadExcel', JsonExcel);
// Vue.use(swal);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  store,
  template: '<App/>'
})



