import {Base64} from 'js-base64'
import axios from 'axios'

axios.defaults.timeout = 6000

var CallAPI = {
    getGoogleSheets : async function (){
        return new Promise((resolve, reject) => {
            axios.get('https://docs.google.com/spreadsheets/d/e/2PACX-1vTafHo-WJlG1-6IBM0VAdUUzo9_5kNjpOFnC7wV3ZHSVc1IFJU9NTEQ4LD7MhAvPFwS0MW_sZY2Zacj/pub?output=csv').then(result => {
                resolve(result.data);
            }),err => {
                reject(err);
            }
        });
    }
}
export default CallAPI = CallAPI;


